import { useDrag } from "react-use-gesture";
import { animated } from "react-spring";
import useStyles from "../../styleContext";
import "./Editor.css";
function Editor() {
    const [props, api, img] = useStyles();
    const bind = useDrag(
        (e) => {
            api.start({
                left: e.offset[0],
                top: e.offset[1],
            });
        },
        {
            rubberband: true,
            bounds: { top: 0, left: 0 },
        }
    );
    return (
        <div className="editor">
            <div className="container">
                {img ? (
                    <>
                        <img
                            src={URL.createObjectURL(img)}
                            alt="heyt"
                        />
                        <animated.div
                            className="text"
                            style={props}
                            {...bind()}
                        >
                            Hello
                        </animated.div>
                    </>
                ) : (
                    "Please upload an image"
                )}
            </div>
        </div>
    );
}

export default Editor;
